import { Container } from '@mui/system';
import React, { useState, useEffect } from 'react';
import FadeLoader from "react-spinners/FadeLoader";
import './App.css';
import { PieChart, Pie, Cell, Label } from 'recharts';
import { LineChart, Line, XAxis, YAxis, Tooltip, } from 'recharts';
import { ResponsiveContainer, CartesianGrid } from 'recharts';
import { MapContainer, Marker, TileLayer, Popup } from 'react-leaflet';

let max = 90; //maximln poet aut z  nastavenho intervalu na parkoviti
let min = 0; //minimln poet aut z  nastavenho intervalu na parkoviti

let statText = "Statisky za poslednch 5 hodin"; //text zobrazen nad grafem

let vyska;
vyska = 500;
if (window.innerWidth < 800) 
vyska = 300;

// React Leaflet Mapka zobrazujc msto kde se nachz parkovit
const Mapka = () => {

  const [center] = useState([50.6813617, 14.0078506]); // Pozoce parkovit v UL
  return (
    <MapContainer center={center} zoom={15} className="map-container">
        <TileLayer
          //url = 'https://tile.openstreetmap.org/{z}/{x}/{y}.png'
          url='https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png'
          attribution= '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors'
          styles="mapbox://styles/mapbox/dark-v10"
        />
        <Marker position={center}>
        <Popup>Parkovit</Popup>
        </Marker>.
    </MapContainer>
  );
}


// Graf zobrazujc historii obsazenosti parkovit v danm intervalu
function CarCount(props)  {
  const [value] = useState(props.count);

  const data = [
      { name: 'Group A', value: value },
      { name: 'Group B', value: 90-value },
    ];
    const COLORS = ['#27beff', '#282c34'];

  return (
    <>
    <PieChart width={300} height={300}>
      <Pie
        data={data}
        cx={150}
        cy={150}
        innerRadius={100}
        outerRadius={130}
        fill="#8884d8"
        paddingAngle={5}
        dataKey="value"
        startAngle={90}
        endAngle={450}
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
    <Label
    value={`Obsazeno`}
    position="center"
    dy={-50}
    dx={-10}
    style={{ fontSize: 20, fill: '#ffffff'}}
  />
  <Label
    value={`${value}`}ffffff
    position="center"
    style={{ fontSize: 80, fill: '#ffffff' }}
  />
  <Label
    value={`z 90`}
    position="center"
    dy={50}
    dx={10}
    style={{ fontSize: 20, fill: '#ffffff'}}
  />
      </Pie>
    </PieChart>
    </>
  );
};

//Aplikace
function App() {
  const [dataG, setDataG] = useState([
  ]); //data pro graf

  let [loading, setLoading] = useState(true); //nastaven e se pi natn strnky zobraz loading

  let [miniLoading, setMiniLoading] = useState(false); //nastaven e se pi natn grafu zobraz loading

  const [parkingData, setParkingData] = useState([]); //data pro aktuln poet aut na parkoviti

  useEffect(() => { //naten dat z API
    const fetchData = async () => {
      try {
        const responseNOW = await fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=1m&window=1m'); //aktuln poet aut na parkoviti
        const response = await fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=5h&window=30m'); //za poslednch 5 hodin po 30 min
        const data = await response.json();
        const dataNOW = await responseNOW.json();
        const mappedData = data.data.map(item => ({ //mapovn dat pro graf ve sprvnm formtu
          name: parseInt(item.time.substring(11, 13)) + 2 + ":" + item.time.substring(14, 16),
          aut: item.full,
        }));
        max = Math.max(...mappedData.map(item => item.aut)); //nastaven maximlnho a minimlnho potu aut na parkoviti
        min = Math.min(...mappedData.map(item => item.aut));
        setDataG(mappedData); //nastaven dat pro graf
        setParkingData(dataNOW.data); //nastaven dat pro aktuln poet aut na parkoviti
        setLoading(false); //pot co probhne API request se nastav loading na false
      } catch (error) {
        console.error(error);
      }
    }
    fetchData();
  }, []);

  function Show5H(){
    statText = "Statisky za poslednch 5 hodin";
    setMiniLoading(true);
    const response = fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=5h&window=30m');
    response.then(res => res.json()).then(data => {
      const mappedData = data.data.map(item => ({
        name: parseInt(item.time.substring(11, 13)) + 2 + ":" + item.time.substring(14, 16),
        aut: item.full,
      }));
      max = Math.max(...mappedData.map(item => item.aut));
      min = Math.min(...mappedData.map(item => item.aut));
      setDataG(mappedData);
      setMiniLoading(false);
    });
  }

  function Show1D(){
    statText = "Statisky za poslednch 24 hodin";
    setMiniLoading(true);
    const response = fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=1d&window=2h');
    response.then(res => res.json()).then(data => {
      const mappedData = data.data.map(item => ({
        name: parseInt(item.time.substring(11, 13)) + 2 + ":" + item.time.substring(14, 16),
        aut: item.full,
      }));
      max = Math.max(...mappedData.map(item => item.aut));
      min = Math.min(...mappedData.map(item => item.aut));
      setDataG(mappedData);
      setMiniLoading(false);
    });
  }

  function Show1W(){ 
    statText = "Statisky za poslednch 7 dn";
    setMiniLoading(true);
    const response = fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=1w&window=1d');
    response.then(res => res.json()).then(data => {
      const mappedData = data.data.map(item => ({
        name: parseInt(item.time.substring(8, 10)) + "." + item.time.substring(5, 7),
        aut: item.full,
      }));
      max = Math.max(...mappedData.map(item => item.aut));
      min = Math.min(...mappedData.map(item => item.aut));
      setDataG(mappedData);
      setMiniLoading(false);
    });
  }

  function Show1M(){
    statText = "Statisky za poslednch 30 dn";
    setMiniLoading(true);
    const response = fetch('https://parkingapi.node.cloud.bagros.eu/getdata/usti_pennyrondel/?timerange=1m&window=1d');
    response.then(res => res.json()).then(data => {
      const mappedData = data.data.map(item => ({
        name: parseInt(item.time.substring(8, 10)) + "." + item.time.substring(5, 7),
        aut: item.full,
      }));
      max = Math.max(...mappedData.map(item => item.aut));
      min = Math.min(...mappedData.map(item => item.aut));
      setDataG(mappedData);
      setMiniLoading(false);
    });
  }

  return (<>
  <Container maxWidth="xl">
  {loading ? <div className="loading"><FadeLoader color="#27beffcf"/></div>: 
    <div className="Container">
      <div className="LeftContainer">
        <h1>st nad Labem - Penny Rondel</h1>	
        <div className="TopContainer">
          <div className="CarCount">
          <CarCount count={parkingData[0].full}/>
          </div>
          <div className="infoDiv">
            <h2>Informace o parkoviti</h2>
            <p>Pln msta: {parkingData[0].full}</p> <br/>
            <p>Voln msta: {90 - parseInt(parkingData[0].full)}</p>	<br/>
            <p>Obsazenost: {Math.round(parkingData[0].full / 90 * 100)}%</p><br/>
            <p>Posledn aktualizace: {(parseInt(parkingData[0].time.substring(11, 13)) + 2 )+ ":" + parseInt(parkingData[0].time.substring(14, 16))}</p>
          </div>
        </div>

        <Mapka/>

     </div>

      <div className="RightContainer">
        <h1>{statText}</h1>
        
        <ResponsiveContainer width={"100%"} height={vyska} className="Graf">
        {miniLoading ? <div className="miniLoading"><FadeLoader color="#27beffcf"/></div>: 
          <LineChart data={dataG}>
          <Line type="monotone" dataKey="aut" stroke="#27beff" strokeWidth={3}/>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis 
            domain={[min -1, max + 1]}
          />
          <Tooltip contentStyle={{backgroundColor: "black"}} itemStyle={{ color: "white" }} />
          </LineChart>
                  }
        </ResponsiveContainer>


        <button onClick={Show5H}>Za 5 hodin</button>

        <button onClick={Show1D}>Za den</button>

        <button onClick={Show1W}>Za tden</button>

        <button onClick={Show1M}>Za msc</button>
      </div>
    </div>
    }
    </Container>
    </>
  );
}

export default App;